import citySelect from '@/components/HOME/uni-location/linzq-citySelect/linzq-citySelect.vue'
import popupLayer from '@/components/HOME/uni-location/popup-layer/popup-layer.vue'
import $lyfw from '@/common/LYFW/LyfwFmq.js' //旅游服务
export default {
	data() {
		return {
			searchIndex: 0, //搜索框是否启用状态值
			searchValue: '', //搜索框值
			searchData: '', //搜索后的值
			
			sixPalaceList: [], //六宫格列表
			screenIndex: 0, //筛选框默认值
			priceOrder: 0, //1 价格从低到高  2价格从高到低
			cateMaskState: 0, //分类面板展开状态
			
			scenicList: [], //景区列表
			scenicListCate : [],//筛选后的景区列表
			scenicListIndex : 10, //列表默认数量
			
			loadingType: 0, //加载更多状态
			loadingText:{
				down :'上拉加载更多',
				refresh : '正在加载...',
				nomore : '没有更多了',
			},
			
			cateId: 0, //已选三级分类id
			cateList: [], //分类数组
			cateValue : '', //分类筛选值
			
			regionWeixin: '请选择', //微信地区数值
			regionApp : '请选择',//APP地区数值
		}
	},
	
	components: {
		citySelect,
		popupLayer
	},
	
	mounted() {
		this.$refs.popupRef.close();
	},
	
	onLoad:function(options) {
		this.cateId = options.tid;
		this.loadCateList(options.fid, options.sid);
		this.Getpostion();
	},
	
	onPullDownRefresh:function(){
		this.lyfwData(); //请求接口数据
	},
	
	onReachBottom() {
		this.getMore();
	},
	
	methods: {
		//请求模拟接口数据
		lyfwData:function() {
			// console.log(this.regionWeixin)
			// 六宫格
			console.log($lyfw.Interface.spt_GetticketSearchByrequestArea_Six.value)
			uni.request({
				url:$lyfw.Interface.spt_GetticketSearchByrequestArea_Six.value,
				method:$lyfw.Interface.spt_GetticketSearchByrequestArea_Six.method,
				data:{ 
					// requestArea : this.regionWeixin,
					requestArea : '南平市'
				},
				// header: {'content-type': 'application/x-www-form-urlencoded'},
				success:(res) => { 
					console.log(res)
					
					if(res.data.msg == '搜索景区信息成功！'){
						this.sixPalaceList = res.data.data; 
					}else if(res.data.msg =='查不到相关景区，请确认景区名！'){
						uni.showToast({
							title:'该地区暂无景点信息', 
							icon:'none'
						})
					}
					
				},
				fail:function(ee){
					console.log(ee)
				}
			})
			
			// 请求景区列表
			uni.request({
				url:$lyfw.Interface.spt_GetticketSearchByrequestArea.value,
				method:$lyfw.Interface.spt_GetticketSearchByrequestArea.method,
				data:{
					// requestArea : this.regionWeixin,
					requestArea : '南平市'
				},
				// header: {'content-type': 'application/x-www-form-urlencoded'},
				success:(res) => {
					// console.log(res)
					if(res.data.msg == '搜索景区信息成功'){
						this.scenicList = res.data.data;
					}else if(res.data.msg =='查不到相关景区，请确认景区名！'){
						uni.showToast({
							title:'该地区暂无景点信息',
							icon:'none'
						})
					}
				}
			})
			setTimeout(()=>{
				uni.stopPullDownRefresh();
			},1000)
		},	
		
		//获取定位数据
		Getpostion:function(){
			setTimeout(()=>{
				uni.getStorage({
					key:'wx_position',
					success:(res)=>{
						// console.log(res)
						this.regionWeixin = res.data;
					},
					complete: () => {
						this.lyfwData(); //请求接口数据
					}
				}),
				
				uni.getStorage({
					key:'app_position',
					success: (res) => {
						// console.log(res)
						if(res.data !== undefined){
							this.regionApp = res.data.city;
						}
					}
				})
				
			},500)
			
		},
		
		//打开地区选择器
		oncity() {
			this.$refs.popupRef.show(); 
		},
		
		//地区获取
		backCity(e) {
			if (e !== 'no' && e !== 'yes') {
				// console.log(e)
				this.regionWeixin = e.cityName
				this.regionApp = e.cityName
				this.$refs.popupRef.close();
				this.lyfwData();
				this.screenIndex = 0;
				this.searchIndex = 0;
			} else if(e == 'yes'){
				uni.getStorage({
					key:'wx_position',
					success:(res)=>{
						// console.log(res)
						this.regionWeixin = res.data;
						this.lyfwData(); //请求接口数据
					}
				}),
				uni.getStorage({
					key:'app_position',
					success: (res) => {
						// console.log(res)
						if(res.data !== undefined){
							this.regionApp = res.data.city;
						}
					}
				})
				this.$refs.popupRef.close();
			}else{
				this.$refs.popupRef.close();
			}
		},
		
		

		//加载分类
		async loadCateList(fid, sid) {
			let list = await this.$api.sceniclist('cateList');
			let cateList = list.filter(item => item.pid == fid);
			cateList.forEach(item => {
				let tempList = list.filter(val => val.pid == item.id);
				item.child = tempList;
			})
			this.cateList = cateList;
		},

		//搜索框-搜索
		searchNow: function() {
			if (this.searchValue == '') {
				uni.showToast({
					title: '未输入搜索关键字',
					icon: 'none',
					duration: 1000
				});
				// retuan false;
			}
			//搜索请求
			uni.hideKeyboard()
			uni.showLoading({
				title:'正在搜索',
			})
			uni.request ({
				url:$lyfw.Interface.spt_GetticketSearchBysearchValue.value,
				method:$lyfw.Interface.spt_GetticketSearchBysearchValue.method,
				data:{
					searchValue : this.searchValue,
				},
				header: {'content-type': 'application/json'},
				
				success : (res) => {
					console.log(res)
					if(res.data.msg =='搜索景区信息成功！'){
						this.searchData = res.data.data;
						this.searchValue = '' 
						this.searchIndex = 1;
						uni.hideLoading()
					}else if(res.data.msg =='查不到相关景区，请确认景区名！'){
						uni.hideLoading() 
						uni.showToast({
							title: '查不到相关景区！如:武夷/武夷山',
							icon: 'none',
							duration: 2000
						});
						this.searchValue = ''
						
					}
				}
			})

		},

		//路由整合
		godetail: function(e) {
			uni.navigateTo({
				url: '/pages/LYFW/scenicSpotTickets/ticketsDetails?ticketId='+JSON.stringify(e)
			})
		},

		//筛选点击
		tabClick(index) {
			if (this.screenIndex === index && index !== 2) {
				return;
			}
			this.screenIndex = index;
			if (index === 2) {
				this.priceOrder = this.priceOrder === 1 ? 2 : 1;
			} else {
				this.priceOrder = 0;
			}
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.clickSork();
		},

		//显示分类面板
		toggleCateMask(type) {
			let timer = type === 'show' ? 10 : 300;
			let state = type === 'show' ? 1 : 0;
			this.cateMaskState = 2;
			setTimeout(() => {
				this.cateMaskState = state;
			}, timer)
		},
		
		//分类点击
		changeCate: function(item) {
			// console.log(item)
			this.cateId = item.id;
			this.cateValue = item.name;
			this.screenIndex = 3;
			this.toggleCateMask();
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			uni.showLoading({
				title:'正在搜索',
			})
			this.clickSork();
			
		},


		//点击排序
		clickSork :function(){
			var sc = this.scenicList;
			this.scenicList = [];
			//筛选，测试数据直接前端筛选了
			if (this.screenIndex == 0) {
				sc.sort((a, b) => a.ticketId - b.ticketId)
				this.cateId = '';
			}
			if (this.screenIndex == 1) {
				sc.sort((a, b) => b.ticketSales - a.ticketSales)
				this.cateId = '';
			}
			if (this.screenIndex == 2) {
				sc.sort((a, b) => {
					if (this.priceOrder == 1) {
						return a.ticketAdultPrice - b.ticketAdultPrice;
					}
					return b.ticketAdultPrice - a.ticketAdultPrice;
				})
				this.cateId = '';
			}
			if(this.screenIndex == 3) {
				let screen = sc.filter(item => {
					return item.ticketTitle == this.cateValue;
				})
				this.scenicListCate = screen;
				uni.hideLoading() 
			}
			
			this.scenicList = this.scenicList.concat(sc);
		},
		 
		//价格转换
		priceConversion:function(data){
			// console.log(data)
			if(data == ''){
				return '开放景区'
			}else{
				return '¥'+data+'元起'
			}
		},
		 

		
		//加载信息
		getMore(){
			this.loadingType = 1;
			
			if(this.scenicListIndex < this.scenicList.length){
				var a = this.scenicListIndex +10;
				this.scenicListIndex = a;
				this.loadingType = 0;
			}
			if(this.scenicListIndex >= this.scenicList.length){
				this.loadingType = 2;
			}
		}

	}
}
